
.tool-select {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  z-index: 1000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgba(7,17,27,0.6);
}
.tool-select.fade-enter-active,
.tool-select.fade-leave-active {
  transition: all 0.2s;
}
.tool-select.fade-enter,
.tool-select.fade-leave-active {
  opacity: 0;
}
.tool-select .tool-box {
  width: 80%;
  background-color: #fff;
}
.tool-select .tool-box .tool-title {
  margin: 0;
  padding: 10px;
  font-size: 16px;
  font-weight: 200;
  color: #d91d36;
  border-bottom: 1px solid #d91d36;
}
.tool-select .tool-box .tool-title .iconfont {
  vertical-align: top;
  margin-right: 8px;
}
.tool-select .tool-box .tool-title .title {
  vertical-align: top;
}
.tool-select .tool-box .tool-content {
  margin: 0;
  padding: 6px 15px;
  width: 100%;
  font-size: 16px;
  line-height: 24px;
  color: #333;
  word-break: break-all;
  max-height: 200px;
  overflow-y: auto;
}
.tool-select .tool-box .btn-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  border-top: 1px solid #efefef;
}
.tool-select .tool-box .btn-wrap .btn {
  padding: 15px 0px;
  width: 50%;
  text-align: center;
  font-size: 16px;
  color: #333;
}
.tool-select .tool-box .btn-wrap .btn-cancel {
  border-right: 1px solid #efefef;
}


