
.cp-dlt {
  position: fixed;
  z-index: 50;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f8f8f0;
}
.cp-dlt.fade-enter-active,
.cp-dlt.fade-leave-active {
  transition: all 0.4s linear;
}
.cp-dlt.fade-enter,
.cp-dlt.fade-leave-active {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}
.cp-dlt .topBar {
  width: 100%;
  height: 40px;
  color: #fff;
  background-color: #d91d36;
}
.cp-dlt .topBar .btn-back {
  padding: 0 10px;
  font-weight: 600;
  font-size: 16px;
  line-height: 40px;
}
.cp-dlt .topBar .title {
  font-size: 17px;
  font-weight: 600;
  line-height: 40px;
  letter-spacing: 2px;
}
.cp-dlt .content {
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
}
.cp-dlt .content .img-404 {
  margin: 30% auto 0px;
  width: 100px;
  height: 100px;
}
.cp-dlt .content .text {
  margin: 20px auto;
  width: 80%;
  font-size: 14px;
  line-height: 24px;
  color: #999;
  letter-spacing: 2px;
}


