
.dt {
  position: fixed;
  z-index: 50;
  top: 40px;
  bottom: 50px;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
  background-color: #f8f8f0;
}
.dt .dt-content .text {
  margin: 0;
  padding: 20px 10px 0px;
  text-align: left;
  font-size: 16px;
  color: #938268;
}
.dt .dt-content .cp-ballType {
  padding: 10px 0;
  width: 100%;
  position: relative;
}
.dt .dt-content .cp-ballType:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid #cdcdcd;
}
.dt .dt-content .cp-ballType:last-child:after {
  display: none;
}
.dt .dt-content .cp-ballType .cp-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  margin: 0 auto;
  width: 280px;
}
.dt .dt-content .cp-ballType .cp-box .ballItem {
  padding-top: 5px;
  width: 35px;
  text-align: center;
}
.dt .dt-content .cp-ballType .cp-box .ballItem .ball {
  display: block;
  width: 33px;
  height: 33px;
  text-align: center;
  font-size: 13px;
  font-weight: 600;
  line-height: 33px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.dt .dt-content .cp-ballType .cp-box .ballItem .whiteBall1 {
  background-image: url("../images/whiteB.png");
  color: #c33f51;
}
.dt .dt-content .cp-ballType .cp-box .ballItem .whiteBall2 {
  background-image: url("../images/whiteB.png");
  color: #3373c1;
}
.dt .dt-content .cp-ballType .cp-box .ballItem .redBall {
  background-image: url("../images/redB.png");
  color: #fff;
}
.dt .dt-content .cp-ballType .cp-box .ballItem .blueBall {
  background-image: url("../images/blueB.png");
  color: #fff;
}


