
.tooltip {
  position: fixed;
  z-index: 1000;
  top: 50%;
  left: 0;
  width: 100%;
  margin-top: -24px;
  text-align: center;
}
.tooltip.fade-enter-active,
.tooltip.fade-leave-active {
  transition: all 0.4s;
}
.tooltip.fade-enter,
.tooltip.fade-leave-active {
  opacity: 0;
}
.tooltip .tooltipText {
  padding: 6px 6px;
  background: #828282;
  border-radius: 10px;
  display: inline-block;
}
.tooltip .tooltipText span {
  padding: 10px 12px;
  color: #fff;
  font-size: 16px;
  background: #1c2023;
  border-radius: 5px;
  display: inline-block;
}


