
.lottery-ssc {
  position: fixed;
  z-index: 50;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f8f8f0;
}
.lottery-ssc.fade-enter-active,
.lottery-ssc.fade-leave-active {
  transition: all 0.4s linear;
}
.lottery-ssc.fade-enter,
.lottery-ssc.fade-leave-active {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}
.lottery-ssc .top-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  z-index: 51;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  background-color: #d91d36;
  color: #fff;
}
.lottery-ssc .top-bar .btn-back {
  padding: 0 10px;
  font-size: 16px;
  line-height: 40px;
  vertical-align: top;
}
.lottery-ssc .top-bar .select-text {
  display: inline-block;
  font-size: 14px;
  width: 20px;
  line-height: 15px;
  padding: 5px;
  margin-right: 12px;
  text-align: center;
  border-left: 1px solid #df4257;
}
.lottery-ssc .top-bar .btn-box {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
.lottery-ssc .top-bar .btn-box .btn-select {
  padding: 3px 5px;
  vertical-align: top;
  border-radius: 2px;
  border: 1px solid #fff;
}
.lottery-ssc .top-bar .btn-box .btn-select .text {
  font-size: 16px;
  line-height: 24px;
}
.lottery-ssc .top-bar .btn-box .btn-select .hhs-icon {
  display: inline-block;
  color: #ffd300;
}
.lottery-ssc .top-bar .btn-box .btn-select .hhs-icon.active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.lottery-ssc .top-bar .btn-history {
  margin-right: 10px;
  font-size: 16px;
  line-height: 40px;
  vertical-align: top;
}
.lottery-ssc .top-bar .select-box {
  position: absolute;
  left: 18%;
  top: 41px;
  z-index: 52;
  width: 60%;
  background-color: #fff;
  border-left: 1px solid #cdcdcd;
  border-right: 1px solid #cdcdcd;
  font-size: 0px;
}
.lottery-ssc .top-bar .select-box .select-item {
  display: inline-block;
  position: relative;
  padding: 5px 0;
  width: 50%;
  font-size: 15px;
  text-align: center;
  color: #938369;
  border-bottom: 1px solid #cdcdcd;
}
.lottery-ssc .top-bar .select-box .select-item.line {
  border-right: 1px solid #938369;
}
.lottery-ssc .top-bar .select-box .select-item .icon {
  display: none;
  position: absolute;
  right: 2px;
  bottom: -10px;
  font-size: 16px;
  color: #c43f52;
}
.lottery-ssc .top-bar .select-box .select-item.active {
  color: #c43f52;
}
.lottery-ssc .top-bar .select-box .select-item.active .icon {
  display: block;
}
.lottery-ssc .bottom-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  z-index: 51;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #181818;
}
.lottery-ssc .bottom-bar .btn-random,
.lottery-ssc .bottom-bar .btn-clear {
  margin: 7px 10px;
  padding: 8px 15px;
  font-size: 18px;
  color: #f7ae2f;
  background: #333;
}
.lottery-ssc .bottom-bar .btn-clear {
  color: #fff;
}
.lottery-ssc .bottom-bar .price-box {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.lottery-ssc .bottom-bar .price-box .price {
  font-size: 14px;
  line-height: 50px;
  color: #fff;
  letter-spacing: 2px;
}
.lottery-ssc .bottom-bar .price-box .price i {
  color: #f2cf6b;
}
.lottery-ssc .bottom-bar .btn-submit {
  margin: 7px 10px;
  padding: 8px 15px;
  font-size: 18px;
  color: #520d08;
  background: -webkit-gradient(linear, 0 0%, 0 100%, from(#fccd01), to(#ff8d08));
}
.lottery-ssc .bottom-bar .cpChoiceBei {
  position: absolute;
  z-index: 52;
  bottom: 50px;
  left: 10px;
  padding: 0px 10px;
  background-color: #fff;
  border: 1px solid #cdcdcd;
  border-bottom: none;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem {
  width: 100%;
  position: relative;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid #000;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem:last-child:after {
  display: none;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem .cpChoiceIcon {
  display: inline-block;
  height: 50px;
  line-height: 50px;
  text-align: center;
  vertical-align: top;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem .cpChoiceIcon img {
  margin-top: 5px;
  width: 40px;
}
.lottery-ssc .bottom-bar .cpChoiceBei .cpChoiceBeiItem .cpChoiceText {
  display: inline-block;
  width: 50px;
  font-size: 18px;
  line-height: 50px;
  text-align: right;
  vertical-align: top;
}


