
.history {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  overflow: hidden;
  background-color: #fff;
}
.history.fade-enter-active,
.history.fade-leave-active {
  transition: all 0.2s linear;
}
.history.fade-enter,
.history.fade-leave-active {
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
}
.history .history-topBar {
  width: 100%;
  height: 40px;
  color: #fff;
  background-color: #d91d36;
}
.history .history-topBar .btn-back {
  padding: 0 10px;
  font-weight: 600;
  font-size: 16px;
  line-height: 40px;
}
.history .history-topBar .title {
  font-size: 17px;
  font-weight: 600;
  line-height: 40px;
  letter-spacing: 2px;
}
.history .history-wrap {
  width: 100%;
  text-align: center;
}
.history .history-wrap .his-header {
  width: 100%;
}
.history .history-wrap .his-header .periods,
.history .history-wrap .his-header .redBall,
.history .history-wrap .his-header .blueBall {
  padding: 10px 0px;
  font-size: 16px;
  color: #83868c;
}
.history .history-wrap .his-header .periods {
  width: 20%;
  border-bottom: 1px solid #cdcdcd;
  border-right: 1px solid #cdcdcd;
}
.history .history-wrap .his-header .periods .iconfont {
  display: inline-block;
  color: #d91d36;
  font-weight: normal;
}
.history .history-wrap .his-header .periods .iconfont.active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.history .history-wrap .his-header .redBall {
  border-bottom: 1px solid #cdcdcd;
  border-right: 1px solid #cdcdcd;
}
.history .history-wrap .his-header .blueBall {
  width: 20%;
  border-bottom: 1px solid #cdcdcd;
}
.history .history-wrap .his-content {
  position: fixed;
  z-index: 101;
  top: 78px;
  bottom: 40px;
  left: 0;
  right: 0;
  width: 100%;
  overflow: hidden;
}
.history .history-wrap .his-content .his-body {
  width: 100%;
  background-color: #f6f5ef;
}
.history .history-wrap .his-content .his-body tr:nth-of-type(even) {
  background-color: #fff;
}
.history .history-wrap .his-content .his-body .periods,
.history .history-wrap .his-content .his-body .redBall,
.history .history-wrap .his-content .his-body .blueBall {
  padding: 10px 0px;
  font-size: 16px;
  color: #83868c;
}
.history .history-wrap .his-content .his-body .periods {
  width: 20%;
  border-right: 1px solid #cdcdcd;
}
.history .history-wrap .his-content .his-body .redBall {
  color: #d91d36;
  border-right: 1px solid #cdcdcd;
}
.history .history-wrap .his-content .his-body .blueBall {
  width: 20%;
  color: #1478dc;
}
.history .history-wrap .his-content .down-load {
  padding: 20px;
  width: 100%;
  text-align: center;
}
.history .history-wrap .his-content .down-load .icon-load {
  width: 20px;
  height: 20px;
}
.history .history-bottomBar {
  position: fixed;
  z-index: 102;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40px;
  text-align: center;
  background-color: #fff;
  border-top: 1px solid #cdcdcd;
}
.history .history-bottomBar .time-text {
  font-size: 16px;
  line-height: 40px;
  vertical-align: top;
}
.history .history-bottomBar .time {
  font-size: 16px;
  line-height: 40px;
  color: #d91d36;
  vertical-align: top;
}


