
.app .top-bar {
  width: 100%;
  height: 40px;
  font-size: 18px;
  line-height: 40px;
  color: #fff;
  text-align: center;
  background-color: #d91d36;
}
.app .lottery-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
  margin-top: 10px;
  position: relative;
  background-color: #fff;
}
.app .lottery-wrap:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid rgba(7,17,27,0.1);
}
.app .lottery-wrap .item-wrap {
  padding: 15px 0;
  width: 50%;
  height: 80px;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.app .lottery-wrap .item-wrap .item {
  display: block;
  width: 100%;
  height: 100%;
  color: #07111b;
}
.app .lottery-wrap .item-wrap .item .icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  margin-left: 16px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  vertical-align: top;
}
.app .lottery-wrap .item-wrap .item .icon.ssc {
  background-image: url("./images/cpIcon1.png");
}
.app .lottery-wrap .item-wrap .item .icon.dlt {
  background-image: url("./images/cpIcon4.png");
}
.app .lottery-wrap .item-wrap .item .icon.d3 {
  background-image: url("./images/cpIcon2.png");
}
.app .lottery-wrap .item-wrap .item .icon.k3 {
  background-image: url("./images/cpIcon3.png");
}
.app .lottery-wrap .item-wrap .item .text {
  font-size: 18px;
  font-weight: 700;
  line-height: 50px;
  vertical-align: top;
  letter-spacing: 4px;
}
.app .lottery-wrap .item-wrap .item:nth-of-type(odd) {
  border-right: 1px solid rgba(7,17,27,0.1);
}
.app .lottery-wrap .item-wrap .item.active {
  color: #07111b;
}


